<template>
  <v-container>
    <div>
      <v-card tile class="mx-auto">
        <v-img
          position="bottom"
          class="white--text"
          height="70px"
          src="@/assets/card-bg.jpg"
        >
          <v-card-text class="text-right text-h6 font-weight-black">
            BRANCH LIST
          </v-card-text>
        </v-img>
      </v-card>

      <v-row class="mt-2">
        <v-col cols="12" md="12">
          <v-card>
            <div class="pl-5 pr-5">
              <v-row>
                <v-col cols="12" sm="12">
                  <v-card-title>
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          color="primary"
                          to="/addBranch"
                          class="text-decoration-none"
                          dark
                          fab
                          v-bind="attrs"
                          v-on="on"
                          small
                        >
                          <v-icon>mdi-plus</v-icon>
                        </v-btn>
                      </template>
                      <span>ADD BRANCH</span>
                    </v-tooltip>
                    <v-spacer></v-spacer>
                    <v-text-field
                      v-model="search"
                      append-icon="mdi-magnify"
                      label="Search"
                      single-line
                      hide-details
                    ></v-text-field>
                  </v-card-title>

                  <v-data-table
                    :headers="headers"
                    :items="branches"
                    :search="search"
                    :hide-default-footer="false"
                  >
                    <template slot="item.numbering" slot-scope="props">
                      {{ props.index + 1 }}
                    </template>

                    <template v-slot:[`item.actions`]="{ item }">
                      <v-chip
                        dark
                        small
                        color="teal lighten-2"
                        @click="editBranch(item.id)"
                        class="mr-1"
                      >
                        <span>EDIT</span>
                      </v-chip>
                      <v-chip
                        dark
                        small
                        color="red darken-1"
                        @click="deleteBranch(item.id)"
                      >
                        <!-- <v-icon>mdi-delete</v-icon> -->
                        <span>DELETE</span>
                      </v-chip>
                    </template>
                  </v-data-table>
                </v-col>
              </v-row>
            </div>
          </v-card>
        </v-col>
      </v-row>
    </div>
  </v-container>
</template>

<script>
import BranchDataService from "../../services/BranchDataService";
export default {
  name: "branch-list",
  data() {
    return {
      branches: [],
      name: "",
      search: "",
      headers: [
        {
          text: "NO.",
          align: "start",
          sortable: false,
          value: "numbering",
          // class: "blue lighten-5",
        },
        {
          text: "BRANCH NAME",
          align: "start",
          sortable: false,
          value: "name",
          // class: "blue lighten-5",
        },
        {
          text: "BRANCH CODE",
          align: "center",
          value: "brCode",
          sortable: false,
          // class: "blue lighten-5",
        },
        {
          text: "ACTIONS",
          align: "center",
          value: "actions",
          sortable: false,
          // class: "blue lighten-5",
        },
      ],
    };
  },
  methods: {
    retrieveBranches() {
      BranchDataService.getAll()
        .then((response) => {
          this.branches = response.data.map(this.getDisplayBranch);
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    refreshList() {
      this.retrieveBranches();
    },

    removeAllBranches() {
      BranchDataService.deleteAll()
        .then((response) => {
          console.log(response.data);
          this.refreshList();
        })
        .catch((e) => {
          console.log(e);
        });
    },

    searchName() {
      BranchDataService.findByBranch(this.name)
        .then((response) => {
          this.branches = response.data.map(this.getDisplayBranch);
          console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    editBranch(id) {
      this.$router.push({ name: "branch-details", params: { id: id } });
    },

    deleteBranch(id) {
      BranchDataService.delete(id)
        .then((response) => {
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.refreshList();
        })
        .catch((e) => {
          console.log(e);
        });
    },

    getDisplayBranch(branch) {
      return {
        id: branch.id,
        name:
          branch.name.length > 30
            ? branch.name.substr(0, 30) + "..."
            : branch.name,
        brCode:
          branch.brCode.length > 30
            ? branch.brCode.substr(0, 30) + "..."
            : branch.brCode,
      };
    },
  },
  mounted() {
    this.retrieveBranches();
  },
};
</script>
